.banner {
  background: #F0F0F0;
  position: relative;

  &__wrapper {
    max-width: 1780px;
    margin: 0 auto;
    
    @media (min-width: 1300px) and (max-width: 1600px) {
      padding: 0 20px;
    }
  }

  .benner__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include phoneScreen {
      display: block;
    }
  }

  .benner__left-block {
    @media (min-width: 1024px) and (max-width: 1280px) {
      margin-left: 35px;
    }
    @include ipadScreen {
      margin-left: 25px;
    }

    @include phoneScreen {
      margin: 0 auto 0;
      padding: 40px 15px 0 15px;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 60px;
    margin-bottom: 50px;

    @media (min-width: 1300px) and (max-width: 1600px) {
      font-size: 60px;
      margin-bottom: 35px;
    }
    @media (min-width: 1300px) and (max-width: 1500px) {
      font-size: 55px;
      margin-bottom: 35px;
    }
    @media (min-width: 1024px) and (max-width: 1280px) {
      font-size: 30px;
      margin-bottom: 30px;
    }
    @include tabletScreen {
      font-size: 46px;
      margin-bottom: 25px;
    }
    @include ipadScreen {
      font-size: 32px;
      margin-bottom: 25px;
    }
    @include phoneScreen {
      font-weight: bold;
      font-size: 32px;
      line-height: 37px;
      margin: 0 auto 28px;
      text-align: center;
    }
  }

  &__subtitle-block {
    display: flex;
    align-items: center;

    @include phoneScreen {
      margin-bottom: 20px;
      justify-content: center;
    }
  }

  &__icon {

    img {
      width: 50px;
      height: 50px;

      @include phoneScreen {
        width: 35px;
        height: 35px;
      }
    }
  }

  &__subtitle {
    font-weight: normal;
    font-size: 32px;
    line-height: 37px;
    color: color(dark);
    max-width: 500px;
    margin-left: 15px;

    @media (min-width: 1024px) and (max-width: 1280px) {
      font-size: 20px;
      line-height: 28px;
      max-width: 350px;
      margin-left: 8px;
    }

    @include ipadScreen {
      font-size: 18px;
      line-height: 22px;
      max-width: 300px;
      margin-left: 5px;
    }

    @include phoneScreen {
      max-width: 235px;
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__right-block {
    height: 100%;

    .dock__mob {
      display: none;

      @include phoneScreen {
        width: 100%;
        display: block;
      }
    }

    .dock__deck {
      @media (min-width: 1700px) and (max-width: 2000px) {
        margin-right: 0;
        display: block;
        width: 740px;
      }
      @media (min-width: 1500px) and (max-width: 1700px) {
        margin-right: 0;
        display: block;
        width: 650px;
      }
      @media (min-width: 1300px) and (max-width: 1500px) {
        width: 570px;
        height: 100%;
        display: block;
      }
      @include tabletScreen {
        width: 500px;
        display: block;
      }
      @include ipadScreen {
        width: 440px;
        display: block;
      }
      @include phoneScreen {
        display: none;
      }
    }
  }
}
