.category {
  margin-bottom: 100px;
  
  @include ipadScreen {
    margin-bottom: 60px;
  }

  &__titile {
    font-weight: bold;
    font-size: 30px;
    text-align: center;
    color: #000;
    margin: 50px 0 30px 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;

    @include phoneScreen {
      display: flex;
      flex-direction: column;
    }
  }
  &__body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #F0F0F0;
    width: 49.3%;
    height: 270px;
    overflow: hidden;

    @include tabletScreen {
      height: 250px;
    }

    @include ipadScreen {
      height: 160px;
    }

    @include phoneScreen {
      width: 100%;
      margin-bottom: 12px;
    }

    @include SEScreen {
      height: 120px;
    }
  }
  &__button {
    display: flex;
    align-items: center;
    background-color: color(red);
    width: 180px;
    height: 80px;
    font-size: 16px;
    color: color(white);
    padding: 0 0 0 15px;
    position: relative;

    @include ipadScreen {
      height: 60px;
    }

    @include SEScreen {
      width: 135px;
      height: 50px;
    }

    &:after {
      content: "";
      position: absolute;
      right: 10px;
      top: 42%;
      background-image: url('../assets/images/arrow.svg');
      background-repeat: no-repeat;
      width: 20px;
      height: 20px;

      @include ipadScreen {
        right: 0;
      }

      @include SEScreen {
        top: 38%;
        right: -3px;
      }
    }
  }
  
  &__image {

    img {
      display: block;

      @include tabletScreen {
        width: 310px;
      }

      @include ipadScreen {
        width: 200px;
      }

      @include SEScreen {
        width: 150px;
      }
    }
  }
}
