.contacts{
  background-color: #F5F5F5;

  @media (min-width: 980px) and (max-width: 1024px) {
    padding-bottom: 0;
  }
  @include tabletScreen {
    padding-bottom: 50px;
  }
  @include phoneScreen {
    padding-bottom: 38px;
  }

  &__wrapper {

  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #F5F5F5;

    @include ipadScreen {
      display: flex;
      flex-direction: column;
      flex-flow: wrap;
    }
  }

  &__info {
    color: #000;
    margin: 0 0 0 12%;

    @include ipadScreen {
      order: 2;
      margin: 50px 0 0 20px;
    }
    @include phoneScreen {
      margin-left: 10px;
    }

    @media (min-width: 1800px) and (max-width: 1920px) {
      margin: 0 0 0 19%
    }
    @media (min-width: 1700px) and (max-width: 1800px) {
      margin: 0 0 0 17%
    }
    @media (min-width: 1600px) and (max-width: 1700px) {
      margin: 0 0 0 14%
    }
    @media (min-width: 1500px) and (max-width: 1600px) {
      margin: 0 0 0 10%
    }
    @media (min-width: 1400px) and (max-width: 1500px) {
      margin: 0 0 0 7%
    }
    @media (min-width: 1200px) and (max-width: 1300px) {
      margin: 0 0 0 4%
    }
    @media (min-width: 980px) and (max-width: 1024px) {
      margin: 0 0 0 4%
    }
  }

  &__title {
    font-weight: bold;
    font-size: 36px;
    color: #000;
    margin-bottom: 30px;
  }

  &__subtitle {
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 25px;
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 50px;
  }

  &__icons {
    display: flex;
    align-items: center;
    color: #000;
  }
  
  .mails {
    margin: 22px 0 33px 0;
  }

  &__phone, &__email {
    margin-right: 12px;
  }

  &__number, &__mail {
    font-weight: bold;
    font-size: 16px;
    color: #000;
  }

  &__email {
  }

  &__button {
    width: 250px;
    height: 51px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    background: #E80A1F;
    color: color(white);

    &:hover {
      transition: all 0.3s;
      background-color: color(dark);
    }
  }

  &__map {
    height: 500px;
    width: 1120px;

    @media (max-width: 1800px) {
      width: 1000px;
    }
    @media (max-width: 1600px) {
      width: 900px;
    }
    @media (max-width: 1400px) {
      width: 830px;
    }
    @media (max-width: 1300px) {
      width: 780px;
    }
    @media (max-width: 1280px) {
      width: 760px;
    }
    @media (max-width: 1024px) {
      width: 650px;
      order: 1;
    }
    @include ipadScreen {
      width: 100%;
    }
    @include phoneScreen {
      height: 290px;
    }
  }

  .maps {
    width: 100%;
    height: 100%;
  }
}
