.footer {
  background: #EFEFEF;

  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 75px 0;

    @include phoneScreen {
      padding: 20px 0;
    }
  }

  &__item {
    display: block;
    width: 250px;
    margin-bottom: 50px;
  }

  &__list {
    margin-bottom: 17px;
  }

  &__title {
    font-weight: bold;
    font-size: 18px;
    color: #000;
    margin-bottom: 30px;
  }

  &__button {
    font-weight: 500;
    font-size: 16px;
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: red;
    border: 1px solid red;
    border-radius: 5px;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__logo {

    img {
      margin-top: 25px;
    }
  }

  &__link {
    font-size: 16px;
    line-height: 18px;
    color: #424242;
    margin-bottom: 17px;
  }

  &-number {
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: #000000;
    margin-bottom: 17px;
  }

  &__mail {
    font-size: 16px;
    line-height: 18px;
    color: #424242;
    margin-bottom: 17px;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -4px;
      background-color: #424242;
      width: 64px;
      height: 2px;
    }
  }

  &__adress {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: capitalize;
    color: #000000;
  }

  &__sub {
    background-color: #fff;
  }

  &__sub-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1280px;
    margin: 0 auto;
    overflow: hidden;
    padding: 30px 40px;

    @include phoneScreen {
     display: flex;
     flex-direction: column;
     padding: 25px 10px 40px 10px;
    }
  }

  &__sub-item {

    @include phoneScreen {
      order: 1;
    }
  }
  .fuuter__sub-cards {

    img {
      margin: 0 5px;

      @include phoneScreen {
        margin: 0;
      }
    }
  }

  &__sub-logos {

    @include phoneScreen {
      order: 0;
      margin-bottom: 25px;
    }
    
    img {
      width: 139px;
      height: 51px;
    }
  }
  
}

//popup
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(19, 28, 8, 0.5);
  backdrop-filter: blur(3px);
  top: 0;
  left: 0;
  z-index: 99;
  opacity: 0;
  overflow-y: auto;
  overflow-x: hidden;
  visibility: hidden;
  transition: all 0.4s;
  align-items: center;
  justify-content: center;

  &.open {
    opacity: 1;
    visibility: visible;
  }

  &__body {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px;
    height: 100%;

    @include phoneScreen{
      align-items: start;
    }
  }

  &__content {
    max-width: 470px;
    padding: 48px 24px 56px;
    opacity: 0;
    transform: perspective(600px) translate(0, -100%) rotateX(45deg);
    transition: all 0.4s;
    background-color: color(white);
    position: relative;

    @include phoneScreen{
      padding: 45px 16px;
    }
  }

  &.open .popup__content {
    opacity: 1;
    transform: perspective(600px) translate(0px, 0%) rotateX(0);
  }

  .close-popup {
    position: absolute;
    top: 20px;
    right: 15px;
    line-height: 18px;
    font-size: 50px;
    color: #E6E7E9;
    cursor: pointer;
  }

  &__heading{
    font-weight: 800;
    font-size: 32px;
    line-height: 39px;
    text-align: center;
    text-transform: uppercase;
    color: color(black);

    @include phoneScreen {
      font-size: 22px;
    } 
  }

  &__text{
    margin-top: 16px;
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
    color: color(black);
  }

  form{

    input{
      width: 100%;
      padding: 8px;
      margin-top: 24px;
      border: none;
      border-bottom: 1px solid #BCBDC0;
      color: color(black);
      font-size: 14px;

      &::placeholder{
        color: #6C6D70;
      }

      &:focus{
        border-width: 2px;
        border-color: color(lightgreen2);
      }

      &.error{
        border-color: red;
      }
    }

    .email-tel-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;

      input{
        width: 204px;
      }

      @include phoneScreen{
        display: block;

        input{
          width: 100%;
        }
      }
    }

    textarea{
      width: 100%;
      height: 81px;
      padding: 8px;
      color: color(black);
      font-size: 14px;
      line-height: 22px;
      border: 1px solid #BCBDC0;

      &::placeholder{
        color: #6C6D70;
      }

      &:focus{
        border-width: 2px !important;
        border-color: color(lightgreen2);
      }
    }

    .inputfile-box{
      border: none;
      padding: 0;
      margin: 0;
      position: relative;

      label {
        width: 280px;
        margin: 20px 0 8px;
        padding: 8px;
        border: none;
        cursor: pointer;
        color: #6C6D70;
        font-size: 14px;
        border-bottom: 1px solid #BCBDC0;
        text-overflow: ellipsis;
        position: relative;

        &:focus{
          border-bottom: 2px solid color(lightgreen2);
        }
    
        &::before {
          content: "Прикрепить";
          position: absolute;
          padding: 5px 0 0 30px;
          right: -48px;
          bottom: 4px;
          width: 24px;
          height: 24px;
          font-size: inherit;
          font-weight: 500;
          color: color(black);
          background-size: cover;
          background: url('../assets/images/upload-icon.svg') no-repeat;
        }
      }
    
      input {
        margin: 0 !important;
        padding: 0;
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        visibility: hidden;
      }

      .file-cleaner{
        position: absolute;
        top: 30px;
        right: 145px;
        width: 14px;
        height: 14px;
        cursor: pointer;
        background: url('../assets/images/file-cleaner.svg') no-repeat;
        background-size: contain;
        z-index: 2;
      }

      p{
        font-size: 11.3px;
        line-height: 17px;
        color: #BCBDC0;
      }

      @include phoneScreen{
        label{
          width: 88%;
          &::after {
            display: none;
          }
  
          &::before {
            content: '';
            bottom: 3px;
            right: -17%;
          }
        }

        .file-cleaner{
          right: 40px;
        }
      }
    }

    .checkbox-box {
      margin: 18px 0 48px;
      display: flex;
      align-items: center;
      
      input {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        z-index: -1;
      }
  
      label {
        padding-left: 37px;
        font-size: 16px;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: color(black);
        cursor: pointer;
        position: relative;
  
        &::before {
          content: "";
          position: absolute;
          left: 0;
          width: 24px;
          height: 24px;
          border: 2px solid color(black);
        }
  
        &.error{
  
          &::before{
            border-color: red;
          }
        }
  
        &::after {
          display: none;
          content: "";
          position: absolute;
          left: 3px;
          top: 4px;
          width: 16.67px;
          height: 16px;
          background: url("../assets/images/checkbox.svg") no-repeat;
        }
      }
  
      input:checked + label {
  
        &::before {
          background-color:black;
          border-color: color(white) !important;
        }
  
        &::after {
          display: block;
          z-index: 2;
        }
      }
  
      @include phoneScreen{
        margin: 24px 0 45px;
  
        label{
          font-size: 12px;
          line-height: 15px;
          padding-left: 36px;
    
          &::before {
            top: 50%;
            transform: translateY(-50%);
          }
    
          &::after {
            top: 50%;
            transform: translateY(-50%);
          }
        }
      }
    }

    .submit{
       width: 100%;
       height: 54px;
       display: flex;
       justify-content: center;
       align-items: center;
       border: 2px solid color(black);
       font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: color(black);

      &:hover{
        background-color: crimson;
        color: white;
        transition: all .3s;
      }
    }
  }
}