.sales {
  margin-bottom: 100px;

  @include ipadScreen {
    margin-bottom: 50px;
  }

  &__title {
    font-weight: bold;
    font-size: 30px;
    color: #000;
    margin: 0 0 30px 0;
    text-align: center;
  }

//mobile__version
&__mobile-container {
  display: none;

  @include phoneScreen {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
}

&__mobile-item {
  width: 160px;
  margin-bottom: 25px;

  @include SEScreen {
    width: 235px;
  }
}

&__mobile-body {

}

&__mobile-bg {
  background: #F6F6F6;
  margin-bottom: 3px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    right: 5px;
    top: 5px;
    background-image: url('../assets/images/00.svg');
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
  }

  img {
    @include SEScreen {
      text-align: center;
      margin: 0 auto;
      display: block;
    }
  }
}
&__mobile-psevdo {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    background-image: url('../assets/images/new.svg');
    background-repeat: no-repeat;
    width: 70px;
    height: 30px;
  }

  &::after {
    content: '';
    position: absolute;
    right: 5px;
    top: 5px;
    background-image: url('../assets/images/00.svg');
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
  }
}

&__mobile-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin-bottom: 5px;
}

&__mobile-price {
  font-size: 12px;
  font-weight: bold;
}

&__mobile-stars {
  margin: 10px 0 18px 0;

  img {
    width: 12px;
    height: 12px;
  }
}

&__mobile-star {
}

&__mobile-reviews {
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
}

&__mobile-button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  background: #22222F;
  color: color(white);
  width: 100%;
  height: 31px;

  &:hover {
    background-color: color(red);
    color: color(white);
    transition: all 0.3s;
  }
}


//decktop version
  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @include ipadScreen {
      display: block;
    }

    @include phoneScreen {
      display: none;
    }
  }

  &__body {
    width: 48.6%;
    box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.37);
    border-top: red;
    margin-bottom: 45px;

    @include ipadScreen {
      width: 100%;
      margin-bottom: 30px;
    }
  }

  &__footer-body {
    box-shadow: none;
  }

  &__backgraund {
    background-color: color(red);
    padding: 0 0 0 40px;
    display: flex;
    justify-content: space-between;

    @include tabletScreen {
      padding: 0 0 0 20px;
    }
  }

  &__footer-bg {
    background: linear-gradient(180deg, #F5F5F5 0%, #F1F1F1 100%);
    color: #000;
  }

  &__footer-star {
    margin-top: 40px;

    @include tabletScreen {
      margin-top: 20px;
    }
  }

  &__calendar {
    color: color(white);
    padding-top: 40px;
  }

  &__calendar-icones {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
  }

  &__calendar-icon {
    margin-right: 10px;
  }

  &__calendar-date {
    font-size: 18px;
  }

  &__footer-date {
    color: color(red);
    font-size: 18px;
  }

  &__calendar-text {
    font-weight: 500;
    font-size: 15px;
    line-height: 20px;
  }

  &__footer-text {
    color: color(dark);
  }

  &__image {

    img {
      display: block;

      @include tabletScreen {
        width: 230px;
        height: 220px;
      }

      @include ipadScreen {
        width: 100%;
        height: auto;
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: end;
    padding: 35px 40px 40px 40px;
  }

  &__bottom-left {
    
  }

  &__bottom-titile {
    font-weight: bold;
    font-size: 26px;
    line-height: 30px;
    color: #000;
    max-width: 278px;
    margin-bottom: 70px;

    @include tabletScreen {
      font-size: 22px;
    }
  }

  &__star-block {
  }

  &__star {
    width: 15px;
    height: 15px;
  }

  &__reviews {
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #000;
    margin-left: 7px;
  }

  &__footer-block {
    display: block;
  } 

  &__footer {
    display: flex;
    justify-content: space-between;
  }

  &__footer-title {
    max-width: 100%;
  }

  &__prices {
    text-align: center;
  }

  &__footer-price {
    display: flex;
    align-items: center;
  }

  &__old-price {
    color: #575757;
    font-size: 25px;
    line-height: 29px;
    margin-bottom: 5px;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      top: 15px;
      right: 62px;
      width: 61px;
      height: 2px;
      background-color: #575757;
    }
  }

  &__footer-old {
    margin-left: 16px;

    @include tabletScreen {
      font-size: 22px;
    }

    &:after {
      right: 14px;

      @include tabletScreen {
        width: 52px;
      }
    }
  }

  &__new-price {
    font-weight: bold;
    font-size: 36px;
    line-height: 41px;
    color: #000;
    margin-bottom: 22px;

    @include tabletScreen {
      font-size: 32px;
    }
  }

  &__footer-new {
    margin: 0;
  }

  &__button {
    width: 170px;
    height: 50px;
    background-color: color(red);
    display: flex;
    justify-content: center;
    align-items: center;
    color: color(white);
    font-weight: 600;
    font-size: 20px;

    &:hover {
      background-color: color(dark);
      transition: all 0.3s;
    }
  }
}