.header {
  background-color: #F5F5F5;

  @include phoneScreen {
    background-color: color(white);
  }

  &__container {
    padding: 30px 0;

    @include ipadScreen {
      padding: 20px 0;
    }
    
    @include phoneScreen {
      padding: 10px 0;
    }
  }

  &__top-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;

    @include ipadScreen {
      margin-bottom: 20px;
    }

    @include phoneScreen {
      margin: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__logo {

    img {
      width: 147px;
      height: 64px;
    }
  }

  &__cart {
    display: none;

    @include phoneScreen {
      display: block;
      width: 30px;
      height: 30px;
      position: relative;
      z-index: 99;

      &::before {
        content: '';
        position: absolute;
        right: 3px;
        top: 11px;
        background-image: url('../assets/images/cart_number.svg');
        background-repeat: no-repeat;
        width: 20px;
        height: 20px;
      }

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__mobile-menu {
    display: none;

    @include phoneScreen {
      display: block;
      position: relative;
      width: 28px;
      height: 16px;
      z-index: 99;

      &::before, &::after, span {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: red;
      }

      &::before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
      span {
        top: 7px;
      }

      &.active {

        span {
          background-color: gray;
          opacity: 0;
          visibility: hidden;
          transition: all 0.2s;
        }

        &::before{
          background-color: gray;
          top: 7px;
          transform: rotate(45deg);
          transition: all 0.2s;
        }

        &::after{
          background-color: gray;
          bottom: 6px;
          transform: rotate(-45deg);
          transition: all 0.2s;
        }
      }
    }
  }

  &__search-block {
    display: flex;

    @include phoneScreen {
      display: none;
    }
  }

  &__input {
    width: 459px;
    border: 1px solid #000;
    padding: 10px 0 10px 10px;

    @include ipadScreen {
      width: 255px;
      padding: 0px 0 0px 5px;
      height: 30px;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 116px;
    padding: 10px 0;
    font-weight: 600;
    background-color: #000;
    color: color(white);

    &:hover {
      transition: all 0.3s;
      background-color: color(red);
    }

    @include ipadScreen {
      font-size: 14px;
      padding: 6px 0;
      width: 90px;
    }
  }

  &__icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 160px;
    position: relative;

    @include phoneScreen {
      display: none;
    }
  }

  .cart-price span {
    position: absolute;
    bottom: -20px;
    right: -12px;
    font-weight: 500;
    color: color(dark);
    letter-spacing: 1px;
    font-size: 16px;

    @include ipadScreen {
      font-size: 13px;
      bottom: -12px;
      right: -13px;
    }

    @include phoneScreen {
      display: block !important;
    }

    &:after {
      content: '';
      position: absolute;
      right: 4px;
      top: -28px;
      background-image: url('../assets/images/cart_number.svg');
      background-repeat: no-repeat;
      width: 20px;
      height: 20px;

      @include ipadScreen {
        top: -25px;
      }
    }
  }

  &__icon {
    margin-left: 40px;
    
    &:first-child {
      margin-left: 0;
    }

    @include ipadScreen {
      margin-left: 25px;
    }

    @include phoneScreen {
      display: none;
    }

    img {
      width: 20px;
      height: 20px;
    }
  }

  &__navigation {

    .header__mobile-search {
      display: none;

      @include phoneScreen {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 40px 0 40px 0;
      }
    }
    @include phoneScreen {
      .mobile__search {
        border: 1px solid #000;
        padding: 0 0 0 5px;
        width: 200px;
        height: 30px;
      }
    }
    @include phoneScreen {
      .mobile__button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70px;
        height: 30px;
        background-color: color(dark);
        color: color(white);
        font-weight: 400;
        font-size: 12px;

        &:hover {
          background-color: red;
        }
      }
    }

    .mobile__icon {
      display: none;
      
      @include phoneScreen {
        display: flex;
        justify-content: space-between;
      }
    }
    @include phoneScreen {
      .mob__icon {
        margin: 0 20px;
      }
    }
  }

  &__menu {
    display: flex;
    justify-content: space-evenly;

    @include ipadScreen {
      display: flex;
      justify-content: space-between;
    }

    @include phoneScreen {
      position: absolute;
      top: -1100%;
      left: 0;
      width: 100%;
      padding: 100px 0 1000px;
      flex-direction: column;
      align-items: center;
      background-color: rgba(19, 28, 8, 0.5);
      backdrop-filter: blur(3px);
      transition: top 0.3s;
      z-index: 10;

      &.active {
        top: 0;
        transition: top 0.3s;
      }

      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 470px;
        background-color: color(white);
        z-index: -1;
        transition: 0.3s;
      }
    }
  }

  &__list {
    @include ipadScreen {
      padding: 8px 0;
    }
  }

  &__link {
    font-weight: 500;
    font-size: 16px;
    color: color(dark);
    
    &:hover {
      color: color(red);
      transition: all 0.3s;
    }
    &:last-child {
      margin-left: 5px;

      @include ipadScreen {
        margin-left: 0;
      }
    }

    @include ipadScreen {
      font-size: 15px;
    }
  }
}
