*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html{
  scroll-behavior: smooth;
}

body {
  font-family: 'Ubuntu', sans-serif;

  &.lock{
    overflow: hidden;
  }

  @include phoneScreen {
    background-color: color(white);
  }
}

ul{
  list-style: none;
}

a {
  text-decoration: none;
}

.wrapper {
  max-width: 1280px; 
  padding: 0 40px;
  margin: auto;
  overflow: hidden;

  // @include tabletScreen{
  //   max-width: 1104px;
  //   margin: 0 auto;
  // }

  @include ipadScreen{
    padding: 0 25px;
  }

  @include phoneScreen{
    padding: 0 15px;
  }

  @media (max-width: 411px) {
    padding: 0 8px;
  }
}